.additionBlock {
  width: 80vw;
  height: 80vh;
  margin: auto;
  background-color: beige;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
}

.newWords {
  width: 100%;
  max-height: 20%;
}

.addition {
  width: 100%;
  height: 30%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: flex-start;
  margin-top: 25px;
}

.words {
  width: 75%;
  display: flex;
}

input, textarea {
  width: 50%;
  height: 40px;
  text-align: center;
  font-size: calc(1vw + 7.5px);
  border: 0.03px solid black;
}

textarea {
  resize: none;
  vertical-align: middle;
}

.lvl {
  width: 50px;
  font-size: calc(0.5vw + 5px);
}

.send {
  width: 100px;
  height: 40px;
}

.showAll {
  display: block;
  width: 80%;
  margin: auto;
  height: 20%;
  display: flex;
  flex-flow: column nowrap;
}

.showAll button {
  width: 200px;
  height: 30px;
  align-self: center;
}

