* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

header {
  position: fixed;
  left: 0vw;
  width: 10vw;
  min-height: 100vh;
  background-color: cadetblue;
  transition: all 0.75s cubic-bezier(0.55, 0.055, 0.675, 0.19);
  font-size: calc(1.5vw + 10px);
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-around;
}

header a {
  color: aliceblue;
  text-decoration: none;
}

header a.active {
  color: gold;
}

.arrow {
  position: absolute;
  width: 30px;
  height: 30px;
  left: calc(10vw - 30px);
  top: 0vw;
  transform: rotate(180deg);
  transition: all 0.75s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.arrow.close {
  left: 10vw;
  transform: rotate(360deg);
}

.close {
  left: -10vw;
}

.testing {
  width: 80vw;
  margin: 0 auto;
}

.info {
  width: 60vw;
  margin: 0 auto;
  border: 10px solid grey;
}

.block {
  width: 100%;
  height: 200px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  font-size: calc(1vw + 6px);
}

.block1,
.block2,
.block3,
.block4,
.block5 {
  border-style: solid;
  border-width: 5px;
}

.block1 {
  border-color: red;
}

.block2 {
  border-color: yellow;
}

.block3 {
  border-color: lime;
}

.block4 {
  border-color: royalblue;
}

.block5 {
  border-color: purple;
}

.block div {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: center;
  /* flex-flow: row wrap;
  justify-content: space-around; */
}

.block div div {
  margin: 5px;
}

div.word {
  display: flex;
  flex-flow: row nowrap;
}

div.word span {
  margin-left: 5px;
}

.tone1 {
  color: red;
}

.tone2 {
  color: yellow;
}

.tone3 {
  color: rgb(6, 217, 6);
}

.tone4 {
  color: blue;
}

